import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/sales/interface/vh-type';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-by-product-app',
  templateUrl: './by-product-app.component.html',
  styleUrls: ['./by-product-app.component.scss']
})
export class ByProductAppComponent implements OnInit {
  listProductApp: Array<any> = [] //sp của app chứa lazadas[]
  listSubProductApp: Array<any> = [] //sp của app chứa lazadas[]
  infoShop: any = {}
  /**----------------------------------- */
  searchValue: any;
  heightScroll: any;
  listPrice: any = {}
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  arrayHidden: Array<any> = [] //mảng này chứa id của những sp đang tạm ẩn
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    public vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef
  ) {
  }
  ngOnInit(): void {
    this.getData()
  }
  getData() {
    this.infoShop = this.vhQuerySales.getlocalShopListLazada()[0];
    this.changeShop(this.infoShop._id)
  }

  /**Chọn shop và lọc sản phẩm theo shop đó 
   * @example this.changeShop('ADDHIWSKDNEFEUWDWGFWER')
  */
  changeShop(id) {
    this.returnArrayHiddenProduct()
    let localProducts: Array<any> = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(e => !e.delete_hidden)
    this.listProductApp = localProducts.map(item => {
      if (item[this.id_branch] && item[this.id_branch].lazadas && item[this.id_branch].lazadas.length) { //filter những sp lazada thuộc id lazada đã chọn
        item[this.id_branch].lazadas = [...item[this.id_branch].lazadas.filter(prod => prod.id_shop == id && !this.arrayHidden.includes(prod.item_id))]
      }
      if (item.lazadas && item.lazadas.length) { //filter những sp lazada thuộc id lazada đã chọn
        item.lazadas = [...item.lazadas.filter(prod => prod.id_shop == id)]
      }
      return item
    })
    this.listSubProductApp = this.listProductApp;
  }
  /**
   * Lấy ra những id của các sản phẩm bị tận ẩn
   * @example   this.returnArrayHiddenProduct()
   */
  returnArrayHiddenProduct() {
    let arr: Array<any> = []
    this.arrayHidden = []
    let localProduct: Array<any> = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(e => !e.delete_hidden)
    for (let product of localProduct) {
      if (product[this.id_branch] && product[this.id_branch].lazadas && product[this.id_branch].lazadas.length) {
        for (let prod of product[this.id_branch].lazadas)
          if (prod.hidden == true && prod.id_shop == this.infoShop['_id']) arr.push(prod.item_id);
      }
    }
    this.arrayHidden = arr;
  }
  trackByFn(index, item) {
    return index;
  }

  /**
   * trờ về trang quản lý cập nhật tồn kho
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/update-stock'])
  }
  /**
   * search tên sản phẩm trong app viethas
   * @example this.search('sản phẩm A')
   */
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.listProductApp = this.vhAlgorithm.searchList(val, [...this.listSubProductApp], ['name'])
    } else this.listProductApp = [...this.listSubProductApp]
  }
  ngAfterViewChecked() {
    if (document.querySelector("#by-product-app-stock") && document.querySelector(".ant-table-thead") && document.querySelector(".by-product-app-stock-header")) {
      this.heightScroll = document.querySelector("#by-product-app-stock").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".by-product-app-stock-header").clientHeight - 90 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**
   * Show/hide sản phẩm con liên kết với sp của app
   * @param productApp 
   * @example this.renderProductLazadas(productApp)
   */
  renderProductLazadas(productApp) {
    let productLazada = this.vhQuerySales.getlocalProductListLazada(this.infoShop['_id'])
    if (!productApp.expand && !productApp.loaded) {
      if (productApp[this.id_branch] && productApp[this.id_branch].lazadas && productApp[this.id_branch].lazadas.length) {
        for (let indexProd in productApp[this.id_branch].lazadas) {
          let prodLazada = productLazada.find(item => item.item_id == productApp[this.id_branch].lazadas[indexProd].item_id)
          if (prodLazada) {
            for (let inndexSku in productApp[this.id_branch].lazadas[indexProd].skus) {
              let sku = prodLazada.skus.find(item => item.SkuId == productApp[this.id_branch].lazadas[indexProd].skus[inndexSku].SkuId);
              if (sku) productApp[this.id_branch].lazadas[indexProd].skus[inndexSku] = { ...productApp[this.id_branch].lazadas[indexProd].skus[inndexSku], ...sku }
            }
            productApp[this.id_branch].lazadas[indexProd] = { ...prodLazada, skus: productApp[this.id_branch].lazadas[indexProd].skus }
          }
        }
        productApp.loaded = true;
      }
    }
    
    productApp.expand = !productApp.expand;
  }



  /**
   * trả về tên trang thái của đơn
   * @param status 
   * @returns string
   * @example let status = this.renderStatusProduct('active')
   */
  public renderStatusProduct(status) {
    switch (status) {
      case 'active': return 'Active';
      case 'live': return 'Live';
      case 'inactive': return 'Inactive';
      case 'deleted': return 'Deleted';
      case 'image-missing': return 'Image missing';
      case 'pending': return 'Pending';
      case 'rejected': return 'Rejected';
      case ' sold-out': return 'Sold out';
    }
  }
  /**Cập nhật từng sp , sau đó get sp từ lazada về kt
   * nếu quantity = với quantity trên lazada thì cập nhật thành công
   * @example this.updateQuantity(product, productApp, sku)
  */
  updateQuantity(product, productApp, sku) {
    sku.loading = true;
    this.vhQuerySales.updateProductByXMLLazada(this.renderFormUpdate(product, sku), this.infoShop['access_token']).then((res: any) => {
      if (res) {
        this.vhQuerySales.getProductDetailLazada([product.item_id], this.infoShop['access_token'], this.infoShop['_id']).then((productLazada: any) => {
          if (this.checkQuantityAfterUpdate(product, productLazada[0], sku)) {
            sku['dateUpdateStock'] = new Date().toISOString()
            this.vhQuerySales.updateMergeLazadasProduct_skus(productApp['_id'], product['item_id'], sku.SkuId,
              { dateUpdateStock: sku['dateUpdateStock'] }, this.infoShop['_id']).then((result) => {
                sku.loading = false
                this.vhComponent.alertMessageDesktop("success", this.lang.translate("Update successfully"))
              })
          } else {
            sku.loading = false
            this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"))
          }
        }).catch(err => console.error(err))
      } else {
        sku.loading = false
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"))
      }
    });
  }
  /**
   * gán giá trị tồn kho mới cho sp của lazada đã lk với sp của app
   * @param product 
   * @param event 
   * @param sku 
   * @example this.checkStock(product, 10 , sku)
   */
  checkStock(product, event: any, sku: any) {
    if (typeof event == 'string')
      delete sku.newQuantity
    else
      sku.newQuantity = event
  }
  /**Kiểm tra xem sau khi update thì quantity có bằng với quantity khi update hay không
   * @example this.checkQuantityAfterUpdate(product, productLazada, sku)
   */
  checkQuantityAfterUpdate(product, productLazada, sku: any) {
    let prod = productLazada.skus.find(item => item.SkuId == sku.SkuId)
    if (prod && prod.quantity == sku.newQuantity) {
      sku.quantity = prod.quantity
      delete sku.newQuantity
      return true
    } else return false
  }
  /**chuyển data theo đúng định dạng update */
  renderFormUpdate(product, sku) {
    return `<Request>
    <Product>
      <Skus>
        <Sku>
          <ItemId>${product.item_id}</ItemId>
          <SkuId>${sku.SkuId}</SkuId>
          <SellerSku>${sku.SellerSku}</SellerSku>
          <quantity>${sku.newQuantity}</quantity>
        </Sku>
      </Skus>
    </Product>
  </Request>`
  }
}
